import { getAdminList, delAdmin, getTotal } from "@/request/admin"
import { MessageBox } from 'element-ui';
export default {
    namespaced: true,
    state: () => ({
        list: [],
        totalNum: 0,   // 总条数
        sizeNum: 5,    // 每页个数
        pageNum: 1     // 页码数
    }),
    getters: {
        adminlist: state => state.list,
        total: state => state.totalNum,
        size: state => state.sizeNum,
        page: state => state.pageNum,
    },
    mutations: {
        SET_LIST(state, data) {
            state.list = data.list;
            state.totalNum = data.total;
        },
        SET_PAGE(state,idx){
            state.pageNum = idx
        }
    },
    actions: {
        async getlist({ commit,state }) {
            let list = await getAdminList(state.pageNum, state.sizeNum);
            let total = await getTotal();
            commit('SET_LIST',{list,total})
        },
        set_page({commit,dispatch},val){
            commit('SET_PAGE',val)
            dispatch('getlist')
        },
        del({ dispatch, state, commit }, id) {
            MessageBox.confirm('确定删除吗？', '提示', {
                confirmButtonText: '删他！',
                cancelButtonText: '不要！',
                type: 'warning'
            }).then(async () => {
                await delAdmin(id);
                // 每页剩余1个的时候，进行删除完了需要往前退1步
                if (state.list.length == 1 && state.pageNum != 1) {
                    let num = state.pageNum;
                    num--;
                    commit('SET_PAGE', num)
                }
                dispatch("getlist")
            })
        }
    }
};


