import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getMenuList(id){
    let res;
    if(id){
        res = await $axios.get('/menuinfo?id='+id);
    }else{
        res = await $axios.get('/menulist?istree=1');
    }
    if(res.list){
        return res.list
    }else{
        return [];
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addMenu(data) {
    let res = await $axios.post('/menuadd',data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
            store.dispatch("menu/getlist")
        })
    }
}



/**
 * 修改数据
 * @param {*} id   修改id
 * @param {*} data   修改的数据
 */
export async function editMenu(id,data) {
    let res =  await $axios.post('/menuedit',{id,...data});
    if (res.code == 200) {
        $Message('success', '修改成功', () => {
            router.back();
            store.dispatch("menu/getlist")
        })
    }
}


/**
 * 删除数据
 * @param {*} id 删除id
 */
export async function delMenu(id) {
    let res = await $axios.post('/menudelete', {id});
    if (res.code==200) {
        $Message('success', '删除成功')
    }
}


