import { host } from "@/common/ApiUrl"
import moment from "moment"
export default{
    formatTime:function(val){
        return  moment(new Date(val * 1)).format('YYYY-MM-DD HH:mm')
    },
    // 返回完整的图片路径
    imgurl:function(val){
        return host+val
    }
}


