// 根getters
export default {
    // menus(state){
    //     return state.user.menus
    // }
    token: (state) => state.user.userinfo.token,
    menus: (state) => state.user.menus,
    menus_url: (state) => state.user.menus_url,
    username: (state) => state.user.userinfo.username,
    haslogin(state){
        if (Object.keys(state.user.userinfo).length){
            return true;
        }else{
            return false;
        }
    }
}


