import { getCateList, delCate } from "@/request/category"
import { MessageBox } from 'element-ui';
export default {
    namespaced: true,
    state: () => ({
        list:[]
    }),
    getters: {
        catelist:state=>state.list
    },
    mutations: {
        SET_LIST(state,data){
            state.list = data;
        }
    },
    actions: {
        async getlist({commit}){
            let list = await getCateList();
            commit('SET_LIST',list)
        },
        del({dispatch},id){
            MessageBox.confirm('确定删除吗？', '提示', {
                confirmButtonText: '删他！',
                cancelButtonText: '不要！',
                type: 'warning'
            }).then(async () => {
                await delCate(id);
                dispatch('getlist')
            })
        }
    }
};


