// 配置axios相关信息

import axios from "axios";

import { Message } from "element-ui";
import store from "@/store"


import { api_url } from "./ApiUrl"

let $axios = axios.create({
    baseURL: api_url,
    timeout:3000
})

// 配置拦截器
// 拦截器需要配置 axios使用 之前！
// 添加请求拦截器
$axios.interceptors.request.use(function (config) {
    
    // 如果是登录请求，就不用携带token。如果不是登录请求，就要携带token，后台进行验证！
    console.log(config)
    if (config.url == "/userlogin"){
        // 在发送请求之前做些什么
        return config;   // 一定要return config ，否则 请求无法成功！
    }else{
        // 不是登录请求，就要给请求头添加上token！
        let { token }  = store.getters;
        // 添加给请求头里面的authorization这个字段！ 实际工作中需要和后端进行沟通！
        config.headers.authorization = token;
        return config
    }

   


}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});

// 添加响应拦截器
$axios.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    if (response.data.code==200){
        return response.data;    // return 内容，否则具体的请求响应里面获取不到数据！
    }else{  // 后台服务器挂了！
        Message.error('请求出错:' + response.data.msg)
        if(response.data.code==403){  // token错误，过期！
            store.dispatch("user/quit")
        }else{
            return response.data; 
        }
    }
   
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
});

// 暴露出去！
export default $axios;






