// 实现数据持久化！
let userinfo = sessionStorage.getItem("userinfo") ? JSON.parse(sessionStorage.getItem("userinfo")) : {};
let menus = Object.keys(userinfo).length ? userinfo.menus : [];
let menus_url = Object.keys(userinfo).length ? userinfo.menus_url : [];

import router from "@/router"

export default {
    namespaced:true,
    state:()=>({
        userinfo: userinfo,
        menus: menus,    // 用户侧边栏菜单信息
        menus_url: menus_url  // 用户可以访问的地址池！
    }),
    mutations:{
        SET_MENUS(state,data){
            state.menus = data;
        },
        SET_MENUS_URL(state,data){
            state.menus_url = data;
        },
        SET_INFO(state,data){
            state.userinfo = data
        },
    },
    actions:{
        quit({commit}) {
            sessionStorage.removeItem("userinfo");
            commit('SET_MENUS',[])
            commit('SET_MENUS_URL',[])
            commit('SET_INFO',{})
            router.push("/login")
        }
    }
}


