import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store';
import { Message} from "element-ui"

// 解决element-ui 当前跳转当前路由报错问题！
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter);

const router = new VueRouter({
  // base: '/admin',    // 基准地址
  // mode: 'history',  // 历史记录模式， 默认是hash模式！
  routes: [
    { 
        path: '/', 
        component: () => import("@/Layout/Index"), 
        meta: { title: '后台首页' },
        children:[  // 嵌套路由，主功能页面都在这个里面！
          { name:"index", path: "/", component: () => import("@/pages/Index"), meta: { title: '后台首页' }},
          // 菜单路由
          { path: "/menu", component: () => import("@/pages/Menu/List"), meta: { title: '菜单列表' } },
          { path: "/menu/add", component: () => import("@/pages/Menu/Info"), meta: { title: '添加菜单' }},
          { path: "/menu/edit/:id", component: () => import("@/pages/Menu/Info"), meta: { title: '修改菜单' }},
          // 角色路由
          { path: "/role", component: () => import("@/pages/Role/List"), meta: { title: '角色列表' }},
          { path: "/role/add", component: () => import("@/pages/Role/Info"), meta: { title: '角色添加' } },
          { path: "/role/edit/:id", component: () => import("@/pages/Role/Info"), meta: { title: '角色修改' }},
          // 管理员路由
          { path: "/admin", component: () => import("@/pages/Admin/List"), meta: { title: '管理员列表' }},
          { path: "/admin/add", component: () => import("@/pages/Admin/Info"), meta: { title: '管理员添加' }},
          { path: "/admin/edit/:id", component: () => import("@/pages/Admin/Info"), meta: { title: '管理员修改' }},
          // 商品分类
          { path: "/category", component: () => import("@/pages/Category/List"), meta: { title: '商品分类列表' } },
          { path: "/category/add", component: () => import("@/pages/Category/Info"), meta: { title: '商品分类添加' } },
          { path: "/category/edit/:id", component: () => import("@/pages/Category/Info"), meta: { title: '商品分类修改' } },
          // 商品规格
          { path: "/specs", component: () => import("@/pages/Specs/List"), meta: { title: '规格列表' } },
          { path: "/specs/add", component: () => import("@/pages/Specs/Info"), meta: { title: '规格添加' } },
          { path: "/specs/edit/:id", component: () => import("@/pages/Specs/Info"), meta: { title: '规格修改' } },
          // 商品
          { path: "/goods", component: () => import("@/pages/Goods/List"), meta: { title: '商品列表' } },
          { path: "/goods/add", component: () => import("@/pages/Goods/Info"), meta: { title: '商品添加' } },
          { path: "/goods/edit/:id", component: () => import("@/pages/Goods/Info"), meta: { title: '商品修改' } },
          // 会员管理
          { path: "/member", component: () => import("@/pages/Member/List"), meta: { title: '会员列表' } },
          // 轮播图管理
          { path: "/banner", component: () => import("@/pages/Banner/List"), meta: { title: '轮播图列表' } },
          { path: "/banner/add", component: () => import("@/pages/Banner/Info"), meta: { title: '轮播图添加' } },
          { path: "/banner/edit/:id", component: () => import("@/pages/Banner/Info"), meta: { title: '轮播图修改' } },
          // 轮播图管理
          { path: "/seckill", component: () => import("@/pages/Seckill/List"), meta: { title: '秒杀列表' } },
          { path: "/seckill/add", component: () => import("@/pages/Seckill/Info"), meta: { title: '秒杀添加' } },
          { path: "/seckill/edit/:id", component: () => import("@/pages/Seckill/Info"), meta: { title: '秒杀修改' } }
        ] 
    },
    { path: '/login', component: () => import("@/pages/Login"), meta: { title: '登录' } },
    { path: '/*', component: () => import("@/pages/NotFound"), meta: { title:'页面走丢了...' } }
  ]
});


// 设置白名单

router.beforeEach((to, from, next)=>{
  // 路由权限拦截！ 从仓库中读取可以访问的 地址池，判断当前的地址是否在地址池中，如果在允许访问，如果不在就拦截！
  
  // 思路：
    // 先判断是否登录！
      // 如果没有登录
          // 访问登录路由， next()
          // 不是登录页，去登录页  next('/login')
      // 如果登录了，
          // 去登录页，那就没有反应。
          // 去非登录页，就判断访问的地址是否在地址池中
                    // 要将  后台首页（/） 注入到地址池中。
                    // 地址是否在地址池中
                          // 完全匹配！   一模一样！
                          // 模糊匹配，前面一致，（确保 / 是放在最后一个）
                          // 假设地址池中有 /menu,  那么  只要以/menu开头的路由都可以进入(这样/menu/add和/menu/edit/:id)
      // 退出就先将登录状态设置为false，就是没有登录，再跳转到登录去
    if(store.getters.haslogin){
        if(to.path!=='/login'){
          let menus_url = store.getters.menus_url;  // 可以访问的地址池！
          menus_url.push("/")
          // 去的地址   /menu/add       
          // 地址池中   /menu
          let str = to.path.substr(1).split("/")
          str = "/"+str[0];
          // 去的地址在地址中或者和地址中模糊匹配满足条件！
          if (menus_url.includes(str)){
            document.title = to.meta.title || '小U商城';
            next();
          }else{
            Message.error('您没有权限访问！')
          }
        }else{
           next("/")
        }
    }else{
        if(to.path=='/login'){
          document.title = to.meta.title || '小U商城';
          next();
        }else{
          next('/login')
        }
    } 

    
})


export  default router;


