import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getCateList(id) {
    let res;
    if (id) {
        res = await $axios.get('/cateinfo?id=' + id);
    } else {
        res = await $axios.get('/catelist?istree=1');
    }
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addCate(data) {
    let res = await $axios.post('/cateadd', data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
            store.dispatch("category/getlist")
        })
    }
}



/**
 * 修改数据
 * @param {*} id   修改id
 * @param {*} data   修改的数据 FormData对象！
 */
export async function editCate(id, data) {
    data.append('id',id)
    let res = await $axios.post('/cateedit',data);
    if (res.code == 200) {
        $Message('success', '修改成功', () => {
            router.back();
            store.dispatch("category/getlist")
        })
    }
}


/**
 * 删除数据
 * @param {*} id 删除id
 */
export async function delCate(id) {
    let res = await $axios.post('/catedelete', { id });
    if (res.code == 200) {
        $Message('success', '删除成功')
    }
}


