// console.log(process.env.NODE_ENV)  // 判断是开发环境还是生成环境！
// process 进程！

let API_URL, HOST;
if (process.env.NODE_ENV == 'development') {  // 开发环境
    API_URL = "/api";
    HOST = "http://localhost:3030"
} else if (process.env.NODE_ENV == 'production') { // 生产环境，部署环境
    API_URL = "http://xxxxxxxx.com/api";
    HOST = "http://xxxxxxxx.com"
}

export let host = HOST;
export let api_url = API_URL;


