
import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getAdminList(page=1,size=10) {
    let res = await $axios.get('/userlist', {
        params: {
            size,
            page
        }
    });
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}

/**
 * 获取某一条信息
 * @param {*} uid
 */
export async function getAdminInfo(uid){
    let res = await $axios.get('/userinfo?uid=' + uid);
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}

/**
 * 获取总条数
 */
export async function getTotal() {
    let res = await $axios.get('/usercount');
    if (res.list) {
        return res.list[0].total;
    } else {
        return 0;
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addAdmin(data) {
    let res = await $axios.post('/useradd', data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
            store.dispatch("admin/getlist")
        })
    }
}



/**
 * 修改数据
 * @param {*} uid   修改uid
 * @param {*} data   修改的数据
 */
export async function editAdmin(uid, data) {
    let res = await $axios.post('/useredit', { uid, ...data });
    if (res.code == 200) {
        $Message('success', '修改成功', () => {
            router.back();
            store.dispatch("admin/getlist")
        })
    }
}


/**
 * 删除数据
 * @param {*} uid 删除的用户id
 */
export async function delAdmin(uid) {
    let res = await $axios.post('/userdelete', { uid });
    if (res) {
        $Message('success', '删除成功')
    }
}

// 登录
export async function login(data){
    let res = await $axios.post('/userlogin',data);
    if(res.code==200){
        // 将用户信息存储在本地存储中（sessionStorage）  实现登录持久化！
        sessionStorage.setItem("userinfo",JSON.stringify(res.list))
        // 把用户信息、菜单、可访问的地址池 存入vuex中！
        store.commit('user/SET_MENUS', res.list.menus);
        store.commit('user/SET_MENUS_URL', res.list.menus_url)
        store.commit('user/SET_INFO',res.list)

        // 跳转到后台首页！
        router.push('/')
        
    }
}



