import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getRoleList(id) {
    let res;
    if (id) {
        res = await $axios.get('/roleinfo?id=' + id);
    } else {
        res = await $axios.get('/rolelist');
    }
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addRole(data) {
    let res = await $axios.post('/roleadd', data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
            store.dispatch("role/getlist")
        })
    }
}



/**
 * 修改数据
 * @param {*} id   修改id
 * @param {*} data   修改的数据
 */
export async function editRole(id, data) {
    let res =  await $axios.post('/roleedit', { id, ...data });
    if(res.code==200){
        $Message('success', '修改成功', () => {
            router.back();
            store.dispatch("role/getlist")
        })
    }
}


/**
 * 删除数据
 * @param {*} id 删除id
 */
export async function delRole(id) {
    let res = await $axios.post('/roledelete', { id });
    if (res) {
        $Message('success', '删除成功')
    }
}


