
import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 增删改查数据请求！


// 获取数据
export async function getSpecsList(page = 1, size = 10) {
    let res = await $axios.get('/specslist', {
        params: {
            size,
            page
        }
    });
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}

/**
 * 获取某一条信息
 * @param {*} id
 */
export async function getSpecsInfo(id) {
    let res = await $axios.get('/specsinfo?id=' + id);
    if(res.list){
        return res.list[0]
    }
}

/**
 * 获取总条数
 */
export async function getTotal() {
    let res = await $axios.get('/specscount');
    if (res.list) {
        return res.list[0].total;
    } else {
        return 0;
    }
}



/**
 * 增加数据
 * @param {*} data   添加的数据
 */
export async function addSpecs(data) {
    let res = await $axios.post('/specsadd', data);
    if (res.code == 200) {
        $Message('success', '添加成功', () => {
            router.back();
            store.dispatch("specs/getlist")
        })
    }
}



/**
 * 修改数据
 * @param {*} id   修改id
 * @param {*} data   修改的数据
 */
export async function editSpecs(id, data) {
    let res = await $axios.post('/specsedit', { id, ...data });
    if (res.code == 200) {
        $Message('success', '修改成功', () => {
            router.back();
            store.dispatch("specs/getlist")
        })
    }
}


/**
 * 删除数据
 * @param {*} id 删除的用户id
 */
export async function delSpecs(id) {
    let res = await $axios.post('/specsdelete', { id });
    if (res) {
        $Message('success', '删除成功')
    }
}



